import { useWindowScroll } from "react-use"
import { Box, Collapse } from "@rp/react/web/mui/core"
import { RpJsonToolbarProps } from "@rp/react/web/rpjson"
import {
  DocumentTitleLarge,
  DocumentTitleSmall,
  DocumentViewerHeaderContainer,
  DocumentViewerSubheader,
  DocumentViewerTitleContainer,
  RpJsonToolbarStyled,
} from "./DocumentViewerHeader.styles"
import { DocumentViewerMetadata, DocumentViewerMetadataProps } from "./DocumentViewerMetadata"

type Props = RpJsonToolbarProps &
  DocumentViewerMetadataProps & { disableShrinking?: boolean; customBackground?: string }

const SCROLL_LIMIT = 30
const DURATION = 0.225

const DocumentViewerHeader = ({
  namespace,
  language,
  documentScope,
  fileExtension,
  onChange,
  rpjson,
  markSentences,
  areMatchesLoading,
  onArrowsClick,
  matchesCount,
  currentMatch,
  onMatchesChange,
  enableMatches,
  actions,
  value,
  foundCount,
  disableShrinking = false,
  customBackground,
}: Props) => {
  const { y: scrollY } = useWindowScroll()
  return (
    <DocumentViewerHeaderContainer>
      <DocumentViewerTitleContainer sx={{ backgroundColor: customBackground }}>
        <Box sx={{ maxWidth: "100%" }}>
          <Collapse in={!disableShrinking && scrollY > SCROLL_LIMIT} timeout={DURATION}>
            <DocumentTitleSmall rpjson={rpjson} />
          </Collapse>
          <Collapse in={disableShrinking || scrollY < SCROLL_LIMIT} timeout={DURATION}>
            <DocumentTitleLarge rpjson={rpjson} />
            <DocumentViewerSubheader>
              <DocumentViewerMetadata
                namespace={namespace}
                rpjson={rpjson}
                documentScope={documentScope}
                language={language}
                fileExtension={fileExtension}
              />
            </DocumentViewerSubheader>
          </Collapse>
        </Box>
      </DocumentViewerTitleContainer>
      <RpJsonToolbarStyled
        value={value}
        onChange={onChange}
        markSentences={markSentences}
        areMatchesLoading={areMatchesLoading}
        actions={actions}
        onArrowsClick={onArrowsClick}
        matchesCount={matchesCount}
        currentMatch={currentMatch}
        onMatchesChange={onMatchesChange}
        enableMatches={enableMatches}
        foundCount={foundCount}
        customBackgroundColor={customBackground}
      />
    </DocumentViewerHeaderContainer>
  )
}

export { DocumentViewerHeader }
